___CSS_LOADER_EXPORT___.push([module.id, `.about-page {
    max-width: 1200px;
    margin: 40px auto;
    padding: 0 20px;
}

.about-header {
    text-align: center;
    margin-bottom: 60px;
}

.about-header h1 {
    font-size: 2.5rem;
    font-weight: 700;
    color: #fff;
    margin-bottom: 15px;
}

.about-header .subtitle {
    font-size: 1.2rem;
    color: #8a9ba8;
}

.about-content {
    display: flex;
    flex-direction: column;
    gap: 60px;
}

.about-section {
    background: #232946;
    border-radius: 18px;
    padding: 40px;
    box-shadow: 0 4px 24px 0 rgba(0, 0, 0, 0.18);
}

.about-section h2 {
    font-size: 1.8rem;
    font-weight: 700;
    color: #fff;
    margin-bottom: 20px;
}

.about-section p {
    color: #b8c1ec;
    font-size: 1.1rem;
    line-height: 1.6;
}

.features-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 30px;
    margin-top: 30px;
}

.feature-card {
    background: #1a1a1a;
    border-radius: 12px;
    padding: 30px;
    text-align: center;
    transition: transform 0.2s, box-shadow 0.2s;
}

.feature-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.22);
}

.feature-icon {
    font-size: 2.5rem;
    margin-bottom: 20px;
    display: block;
}

.feature-card h3 {
    color: #fff;
    font-size: 1.3rem;
    margin-bottom: 15px;
}

.feature-card p {
    color: #b8c1ec;
    font-size: 1rem;
    margin: 0;
}

.team-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 30px;
    margin-top: 30px;
}

.team-member {
    background: #1a1a1a;
    border-radius: 12px;
    padding: 30px;
    text-align: center;
}

.member-avatar {
    font-size: 2.5rem;
    margin-bottom: 20px;
    display: block;
}

.team-member h3 {
    color: #fff;
    font-size: 1.3rem;
    margin-bottom: 15px;
}

.team-member p {
    color: #b8c1ec;
    font-size: 1rem;
    margin: 0;
}

.contact-section {
    text-align: center;
}

.contact-info {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.contact-info p {
    color: #b8c1ec;
    font-size: 1.1rem;
    margin: 0;
}

@media (max-width: 768px) {
    .about-page {
        margin: 20px auto;
    }

    .about-header {
        margin-bottom: 40px;
    }

    .about-header h1 {
        font-size: 2rem;
    }

    .about-section {
        padding: 30px;
    }

    .features-grid,
    .team-grid {
        grid-template-columns: 1fr;
    }
} `, "",{"version":3,"sources":["webpack://./src/styles/tentangKami.css"],"names":[],"mappings":"AAAA;IACI,iBAAiB;IACjB,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,mBAAmB;IACnB,mBAAmB;IACnB,aAAa;IACb,4CAA4C;AAChD;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,2DAA2D;IAC3D,SAAS;IACT,gBAAgB;AACpB;;AAEA;IACI,mBAAmB;IACnB,mBAAmB;IACnB,aAAa;IACb,kBAAkB;IAClB,2CAA2C;AAC/C;;AAEA;IACI,2BAA2B;IAC3B,4CAA4C;AAChD;;AAEA;IACI,iBAAiB;IACjB,mBAAmB;IACnB,cAAc;AAClB;;AAEA;IACI,WAAW;IACX,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,eAAe;IACf,SAAS;AACb;;AAEA;IACI,aAAa;IACb,2DAA2D;IAC3D,SAAS;IACT,gBAAgB;AACpB;;AAEA;IACI,mBAAmB;IACnB,mBAAmB;IACnB,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;IACjB,mBAAmB;IACnB,cAAc;AAClB;;AAEA;IACI,WAAW;IACX,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,eAAe;IACf,SAAS;AACb;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,cAAc;IACd,iBAAiB;IACjB,SAAS;AACb;;AAEA;IACI;QACI,iBAAiB;IACrB;;IAEA;QACI,mBAAmB;IACvB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,aAAa;IACjB;;IAEA;;QAEI,0BAA0B;IAC9B;AACJ","sourcesContent":[".about-page {\r\n    max-width: 1200px;\r\n    margin: 40px auto;\r\n    padding: 0 20px;\r\n}\r\n\r\n.about-header {\r\n    text-align: center;\r\n    margin-bottom: 60px;\r\n}\r\n\r\n.about-header h1 {\r\n    font-size: 2.5rem;\r\n    font-weight: 700;\r\n    color: #fff;\r\n    margin-bottom: 15px;\r\n}\r\n\r\n.about-header .subtitle {\r\n    font-size: 1.2rem;\r\n    color: #8a9ba8;\r\n}\r\n\r\n.about-content {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 60px;\r\n}\r\n\r\n.about-section {\r\n    background: #232946;\r\n    border-radius: 18px;\r\n    padding: 40px;\r\n    box-shadow: 0 4px 24px 0 rgba(0, 0, 0, 0.18);\r\n}\r\n\r\n.about-section h2 {\r\n